@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800);

* {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Roboto", sans-serif;
  color: #212121;
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
  cursor: none;
}
body {
  padding: 24px 98px;
}
/*menu*/
.menu__logo {

}
.menus__list {
  display: flex;
}
.menus__list:nth-child(2) {
  margin-left: 120px;
}
.menu-element__link {
  font-size: 20px;
  margin-right: 14px;
}
.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menus {
  display: flex;
}
.social-link {
  margin-right: 24px;
}
html,
body {
  height: 100%;
  cursor:none;
  overflow: hidden;
}
.cursor-dot,
.cursor-dot-outline {
  z-index: 800;
    pointer-events: none;
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 50%;
    opacity: 0;
    transform: translate(-50%, -50%);
    transition: opacity 0.2s ease-in-out,
                transform 0.2s ease-in-out;
}
.cursor-dot {
  width: 8px;
  height: 8px;
background-color: blue;
}

.cursor-dot-outline {
  width: 40px;
  height: 40px;
  background-color: rgba(0, 0, 255, 0.5);
}
.banner-title {
  transform: translateZ(35px);
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  margin-bottom: 24px;
  text-align: left;
}

.banner-subtitle {
  transform: translateZ(25px);
  font-weight: 300;
  font-size: 40px;
  line-height: 47px;
  text-align: left;
}
.banner-container {
  margin-top: 296px;
  width: 100%;
  display: flex;
  justify-content: flex-start;
}
.banner {
  background: #fff;
  border-radius: 15px;
  display: inline-block;
  perspective: 1800px;
  text-align: center;
  transform-origin: 50% 50%;
  transform-style: preserve-3d;
  transform: rotateX(11deg) rotateY(16.5deg);
  min-width: 800px;
}
.fixed-container {
  position: relative;
}
.wrapper-canvas {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#c {
  position: fixed;
  width: 100%;
  height: 100%;
  display: block;
  z-index: -1;
  bottom: -80px;

}

.loading {
  position: fixed;
  z-index: 50;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #f1f1f1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader {
  -ms-perspective: 120px;
  perspective: 120px;
  width: 100px;
  height: 100px;
  position: fixed;
  right: 120px;
}

.loader:before {
  content: "";
  position: absolute;
  left: 25px;
  top: 25px;
  width: 50px;
  height: 50px;
  background-color: #9bffaf;
  -webkit-animation: flip 1s infinite;
          animation: flip 1s infinite;
}

@-webkit-keyframes flip {
  0% {
    transform: rotate(0);
  }

  50% {
    transform: rotateY(180deg);
  }

  100% {
    transform: rotateY(180deg) rotateX(180deg);
  }
}

@keyframes flip {
  0% {
    transform: rotate(0);
  }

  50% {
    transform: rotateY(180deg);
  }

  100% {
    transform: rotateY(180deg) rotateX(180deg);
  }
}

